@font-face {
  font-family: "Avenir", sans-serif;
  font-weight: 300;
  src: url(./fonts/Avenir-Book.ttf);
}

html {
  font-family: "Avenir", sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 16px;
}

.form {
  position: relative;
  margin-top: 20px;
}

.searchBar {
  font-family: inherit;
  width: 100%;
  height: 70px;
  line-height: 22px;
  padding-left: 62px;

  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  box-shadow: 0 1px 8px 0 rgba(61, 78, 97, 0.1);
}

.searchBar:focus {
  font-weight: bold;
}

.searchBar__small {
  height: 42px;
  padding-left: 33px;

  font-size: 14px;
}

.searchBar:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.searchBar::placeholder {
  color: #3d4e61;
  font-weight: 300;
  font-size: 16px;
}

.searchBar__small::placeholder {
  font-size: inherit;
}

.icon {
  position: absolute;
  width: 19px;
  height: 19px;
  left: 26px;
  top: 25px;
  color: #687c94;
}

.icon__small {
  width: 11px;
  height: 11px;
  left: 13px;
  top: 15px;
}
